import React, { useState, useContext } from "react";
// import MyHeader from "./components/header";
import { makeStyles } from "@material-ui/core/styles";
import LabelBottomNavigation from "./components/footer";
import { UserContext } from "./App";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  video: {
    width: "50%",
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "50px",
    display: "block",
  },
  headers: {
    textAlign: "center",
    marginTop: "100px",
    fontFamily: "Mate SC",
    paddingBottom: "10%",
  },
  subheaders: {
    textAlign: "center",
    fontFamily: "Mate SC",
  },
  table: {
    minWidth: 650,
  },
  tableCon: {
    width: "90%",
    marginLeft: "auto",
    marginRight: "auto",
  },
}));

function ShoppingCart(props) {
  const classes = useStyles();
  const { user, setUser } = useContext(UserContext);
  console.log(user);

  function removeItem(name) {
    setUser((prev) => ({
      ...prev,
      cart: prev.cart.filter(function (obj) {
        return obj.name !== name;
      }),
    }));
  }

  return (
    <React.Fragment>
      <h2 className={classes.headers}>Your items in cart</h2>
      {!user.cart && (
        <h4 className={classes.subheaders}>Please login to see your cart</h4>
      )}
      {user.cart && !user.cart[0] && (
        <h4 className={classes.subheaders}>Please add items to your cart</h4>
      )}
      {user.cart && user.cart[0] && (
        <TableContainer component={Paper} className={classes.tableCon}>
          <Table className={classes.table} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell align="right">Category</TableCell>
                <TableCell align="right">Price</TableCell>
                <TableCell align="right">Remove</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {user.cart.map((row) => (
                <TableRow key={row.name}>
                  <TableCell component="th" scope="row">
                    {row.name}
                  </TableCell>
                  <TableCell align="right">{row.category}</TableCell>
                  <TableCell align="right">{row.price}</TableCell>
                  <TableCell align="right">
                    <Button
                      variant="contained"
                      color="secondary"
                      onClick={() => {
                        removeItem(row.name);
                      }}
                    >
                      x
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <LabelBottomNavigation />
    </React.Fragment>
  );
}

export default ShoppingCart;
